.form {
  gap: 10px;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.9);
  margin: 20px auto;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
  width: 350px;
}

.input {
  text-align: center;
  width: 90%;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #ccc;
  transition: box-shadow 0.3s;
}

.input:hover {
    box-shadow: 0px 0px 6px 0px rgba(124, 124, 124, 0.43);
}

.submitBtn {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  border-radius: 10px;
  color: white;
  background-color: rgb(21, 208, 74);
  margin-top: 10px;
  padding: 0 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.submitBtn:hover {
  background-color: rgb(16, 128, 47);
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
}

.addUserIcon {
  fill: white;
  border: none;
  width: 15px;
  }
