.filter {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 180px;
  font-size: 15px;
  gap: 2px;
  background-color: rgba(255, 255, 255, 0.515);
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.9);
}

.inputFilter {
  text-align: center;
  width: 200px;
  padding: 5px;
  border-radius: 10px;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  transition: box-shadow 0.3s;
}

.inputFilter:hover {
  box-shadow: 0px 0px 6px 0px rgba(124, 124, 124, 0.43);
}
